.container {
	width: 1200px !important;
	@include r(1247) {
		width: 1170px !important; } }
.body-wr {
	background: #151e31 url('../img/bg.png') top center no-repeat;
	position: relative;
	z-index: 1;
	// min-height: 3449px
	min-height: 100vh;
	&:before {
		position: absolute;
		content: '';
		width: 100%;
		height: 2072px;
		bottom: 0;
		background-image: url('../img/bg-bottom.png');
		background-position: bottom center;
		background-repeat: no-repeat;
		z-index: 2; } }
.content-wr {
	position: relative;
	width: 1300px;
	margin: 0 auto;
	z-index: 3;
	@include r(1347) {
		width: 1200px; }
	@include r(1247) {
		width: 1170px; } }
.content {
	background-color: #fff;
	box-shadow: 0 26px 49px rgba(3, 13, 30, 0.33);
	border-bottom: 4px solid #e2e7e8; }

.block,
.sidebar {
	padding: 20px 0 30px; }
.block-bordered {
	border-bottom: 1px solid #eeeff0; }
.block-bg {
	background-color: #f4f8f9;
	box-shadow: 0 1px 0 rgba(226, 230, 231, 0.56); }

.footer {
	padding: 30px 0 45px; }

.icon-calendar {
	display: inline-block;
	width: 24px;
	height: 22px;
	background: url('../img/icons/icon-calendar.png') no-repeat;
	position: relative;
	vertical-align: text-bottom;
	margin-right: 20px; }
.icon-send {
	display: inline-block;
	width: 24px;
	height: 21px;
	background: url('../img/icons/icon-send.png') no-repeat;
	position: relative;
	vertical-align: text-bottom;
	margin-right: 20px; }
.is-video-icon {
	display: inline-block;
	width: 17px;
	height: 12px;
	background: url('../img/icons/icon-video.png') no-repeat;
	position: relative;
	top: 2px;
	margin-right: 5px; }



