.sb-search {
	position: absolute;
	right: 0;
	top: 0;
	width: 0%;
	min-width: 50px;
	height: 50px;
	float: right;
	overflow: hidden;
	-webkit-transition: width 0.3s;
	-moz-transition: width 0.3s;
	transition: width 0.3s;
	-webkit-backface-visibility: hidden; }
.sb-search-input {
	position: absolute;
	top: 0;
	right: 0;
	border: none;
	outline: none;
	background: transparent;
	width: 100%;
	height: 50px;
	margin: 0;
	z-index: 10;
	padding: 20px 65px 20px 55px;
	color: #646673;
	font-family: FiraSans;
	font-size: 17px;
	font-weight: 300;
	color: #2c3e50;
	&::-webkit-input-placeholder {
		color: transparent; }
	&:-moz-placeholder {
		color: transparent; }
	&::-moz-placeholder {
		color: transparent; }
	&:-ms-input-placeholder {
		color: transparent; } }
input[type="search"].sb-search-input {
	-webkit-appearance: none;
	-webkit-border-radius: 0px; }
.sb-icon-search,
.sb-search-submit {
	width: 50px;
	height: 50px;
	display: block;
	position: absolute;
	right: 0;
	top: 0;
	padding: 0;
	margin: 0;
	line-height: 50px;
	text-align: center;
	cursor: pointer; }
.sb-search-submit {
	background: #fff;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
	opacity: 0;
	color: transparent;
	border: none;
	outline: none;
	z-index: -1; }
.sb-icon-search {
	color: #fff;
	background: transparent;
	z-index: 90;
	font-size: 22px;
	font-family: 'Glyphicons Halflings';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	-webkit-font-smoothing: antialiased;
	&:before {
		content: "\e003"; } }
.sb-search.sb-search-open,
.no-js .sb-search {
	width: 100%; }
.sb-search.sb-search-open {
	.sb-icon-search {
		color: #4b5967; }
	.sb-search-input {
		background-color: #fff;
		box-shadow: 0 12px 40px rgba(0, 0, 0, 0.07);
		&::-webkit-input-placeholder {
			color: #646673; }
		&:-moz-placeholder {
			color: #646673; }
		&::-moz-placeholder {
			color: #646673; }
		&:-ms-input-placeholder {
			color: #646673; } } }
.sb-search.sb-search-open .sb-icon-search,
.no-js .sb-search .sb-icon-search {
	background: transparent;
	color: #4b5967;
	z-index: 11; }
.sb-search.sb-search-open .sb-search-submit,
.no-js .sb-search .sb-search-submit {
	z-index: 90; }

