  width: 100%;
  vertical-align: bottom;
}

.gallery-item a {
  display: block;
  position: relative;
  background-color: #060f1c;
}

.gallery-item a:before {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-image: url(../img/icons/icon-zoom.png);
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
  z-index: 2;
}

.gallery-item a:hover:before {
  opacity: 1;
}

.gallery-item a:hover img {
  opacity: .57;
}

.nav-footer {
  padding: 40px 0 0;
}

.nav-footer ul {
  margin: 0;
  padding: 0;
}

.nav-footer ul li {
  display: inline-block;
  margin-right: 20px;
  list-style-type: none;
}

.nav-footer ul li a {
  color: #fff;
  font-family: FiraSans;
  font-size: 15px;
  font-weight: 500;
}

.copy {
  color: #8e95a3;
  font-family: FiraSans-Book;
  font-size: 13px;
  font-weight: 300;
}

.social {
  text-align: right;
  padding: 45px 0 0;
}

.social ul {
  margin: 0;
  padding: 0;
}

.social ul li {
  display: inline-block;
  margin-left: 10px;
  list-style-type: none;
}

.social-item {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-image: url("../img/icons/icons-social.png");
  background-repeat: no-repeat;
}

.social-item.fb {
  background-position: 0 0;
}

.social-item.vk {
  background-position: -34px 0;
}

.social-item.od {
  background-position: -68px 0;
}

.swiper-button-next,
.swiper-button-prev {
  width: 8px;
  height: 13px;
  margin-top: 0;
  z-index: 10;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transform: translateY(-50%);
}

.swiper-button-prev {
  left: auto;
  right: 30px;
}

.swiper-button-next,
.swiper-container-rtl .swiper-button-prev {
  background-image: url("../img/icons/icon-arrow-next.png");
}

.swiper-button-prev,
.swiper-container-rtl .swiper-button-next {
  background-image: url("../img/icons/icon-arrow-prev.png");
}

.modal-dialog .form-group:last-child {
  margin-bottom: 0;
}

.modal-dialog .form-group-bg {
  background-color: #2f7cc9;
}

.modal-dialog .modal-content {
  border: 0;
}

.gallery .news-block {
  padding-bottom: 15px;
  margin-bottom: 15px;
}

.dfw {
  display: flex;
  flex-wrap: wrap;
}
