@include font-face("FiraSans", "../fonts/FiraSans-Light", 300);
@include font-face("FiraSans", "../fonts/FiraSans-Regular", 400);
@include font-face("FiraSans", "../fonts/FiraSans-Medium", 500);
@include font-face("FiraSans", "../fonts/FiraSans-Bold", 700);
@include font-face("FiraSans", "../fonts/FiraSans-Heavy", 800);
@include font-face("FiraSans-Book", "../fonts/FiraSans-Book", 300);
@include font-face("Lato", "../fonts/Lato-Medium", 500);
@include font-face("Lato", "../fonts/Lato-Bold", 700);
@include font-face("Lato", "../fonts/Lato-Black", 900);

body {
	line-height: 1.4;
	color: #535355;
	font-family: FiraSans;
	font-size: 14px;
	font-weight: 300; }
a {
	&:hover,
	&:focus,
	&:active {
		text-decoration: none; } }

img {
	max-width: 100%; }

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
	font-family: inherit;
	font-weight: 500;
	line-height: 1.4;
	color: inherit; }
p {
	margin: 15px 0; }
